// components/landlord-registration-debug.tsx (Temporary for debugging)
'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { 
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage 
} from '@/components/ui/form';
import { createClient } from '@/lib/supabase/client';
import { Alert, AlertDescription } from '@/components/ui/alert';

const simpleSchema = z.object({
  email: z.string().email("Please enter a valid email address"),
  password: z.string().min(6, "Password must be at least 6 characters"),
});

type SimpleFormValues = z.infer<typeof simpleSchema>;

export default function DebugRegistration() {
  const router = useRouter();
  const supabase = createClient();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [debugInfo, setDebugInfo] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  const form = useForm<SimpleFormValues>({
    resolver: zodResolver(simpleSchema),
    defaultValues: {
      email: '',
      password: '',
    },
  });

  const onSubmit = async (data: SimpleFormValues) => {
    setIsSubmitting(true);
    setError(null);
    setDebugInfo(null);

    try {
      console.log('Attempting to sign up with:', data.email);
      
      // Test 1: Check if Supabase client is working
      const { data: sessionData } = await supabase.auth.getSession();
      console.log('Current session:', sessionData);
      
      // Test 2: Try to sign up
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: data.email,
        password: data.password,
        options: {
          emailRedirectTo: `${window.location.origin}/auth/callback`,
        },
      });

      console.log('Auth response:', { authData, authError });

      if (authError) {
        throw new Error(`Auth Error: ${authError.message}`);
      }

      if (!authData.user) {
        throw new Error('No user returned from signup');
      }

      console.log('User created:', authData.user.id);

      // Test 3: Try to insert into admin table
      const adminData = {
        id: authData.user.id,
        full_name: 'Test User',
        email: data.email,
        phone_number: '1234567890',
        role: 'landlord',
        is_active: true,
      };

      const { error: dbError } = await supabase
        .from('admin')
        .insert(adminData);

      console.log('Database insert result:', { dbError });

      if (dbError) {
        throw new Error(`Database Error: ${dbError.message}`);
      }

      setDebugInfo({
        success: true,
        userId: authData.user.id,
        email: authData.user.email,
        session: authData.session,
      });

      // Store in localStorage for debugging
      localStorage.setItem('debugRegistration', JSON.stringify({
        userId: authData.user.id,
        email: data.email,
        timestamp: new Date().toISOString(),
      }));

    } catch (error: any) {
      console.error('Full error:', error);
      setError(error.message);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center p-4">
      <Card className="w-full max-w-md">
        <CardHeader>
          <CardTitle>Debug Registration</CardTitle>
        </CardHeader>
        <CardContent>
          {error && (
            <Alert variant="destructive" className="mb-4">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {debugInfo && (
            <Alert className="mb-4 bg-green-50">
              <AlertDescription className="text-green-800">
                Success! User ID: {debugInfo.userId}
              </AlertDescription>
            </Alert>
          )}

          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
              <FormField
                control={form.control}
                name="email"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Email</FormLabel>
                    <FormControl>
                      <Input 
                        type="email" 
                        placeholder="test@example.com" 
                        {...field} 
                        disabled={isSubmitting}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="password"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Password</FormLabel>
                    <FormControl>
                      <Input 
                        type="password" 
                        placeholder="Password" 
                        {...field} 
                        disabled={isSubmitting}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <Button type="submit" disabled={isSubmitting} className="w-full">
                {isSubmitting ? 'Testing...' : 'Test Registration'}
              </Button>
            </form>
          </Form>

          <div className="mt-6 p-4 bg-gray-50 rounded">
            <h3 className="font-semibold mb-2">Debug Info:</h3>
            <pre className="text-xs overflow-auto">
              {JSON.stringify({
                envUrl: process.env.NEXT_PUBLIC_SUPABASE_URL?.substring(0, 30) + '...',
                envKey: process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY?.substring(0, 10) + '...',
                location: window.location.origin,
              }, null, 2)}
            </pre>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}